ion-content {
  --background: var(--aguamarica) var(--bkg-money) no-repeat center bottom / 100%;
}

.ion-content {
  &--pink {
    --background: var(--guayaba) var(--bkg-pink) no-repeat center bottom / 100%;
  }

  &--white {
    --background: var(--white) var(--bkg-white) no-repeat center bottom / 100%;
  }

  &--closet {
    --background: var(--aguamarica) var(--bkg-closet) no-repeat center bottom / 100%;
  }

  &--movements {
    --background: var(--white) var(--bkg-white) no-repeat center bottom / 100%;
  }

  &--origin {
    --background: var(--medianoche) var(--bkg-origin) no-repeat center bottom / 100%;
  }

  &--registry {
    --background: var(--medianoche) var(--bkg-registry) no-repeat center bottom / 100%;
  }

  &--modal {
    --background: var(--white) no-repeat center bottom / 100%;
  }

  &--goal {
    --background: var(--white) var(--bkg-goals) no-repeat center top / 100%;
  }

  &--goal-accomplished {
    --background: var(--aguamarica) var(--bkg-goal-accomplished) no-repeat center top / 100%;
  }

  &--pockets {
    --background: var(--white) var(--bkg-pockets) no-repeat center top / 100%;
  }

  &--empty-case {
    --background: var(--white);
  }

  &--enroll {
    --background: var(--white) var(--bkg-enroll) no-repeat center bottom / 100%;
  }

  &--dark {
    --background: var(--medianoche) var(--bkg-dark) no-repeat center bottom / 100%;
  }

  &--home {
    --background: var(--medianoche) var(--bkg-home) center/cover no-repeat;
  }

  &--backpack {
    --background: var(--white) var(--bkg-backpack) no-repeat center top / 100%;
  }

  &--backpack-contingency {
    --background: var(--white) var(--bkg-backpack-contingency) no-repeat center top / 100%;
  }

  &--backpack-without-ceiling {
    --background: var(--white) var(--bkg-backpack-without-ceiling) no-repeat center top / 100%;
  }
}
